<template>
    <div page-index>
        <div v-shadow="{ radius: 8 }">
            <AreaVue />
        </div>
        <div
            v-shadow="{ radius: 8 }"
            style="margin-top: 1rem">
            <ReceiveVue />
        </div>
        <div
            v-card="450"
            data-has-a>
            <p>关于消息订阅与发布</p>
            <div>
                <pre>
官方地址: [<a href="https://sahadar.github.io/pubsub/" target="_blank">https://sahadar.github.io/pubsub/</a>]
安装:
  npm i pubsub.js
使用:
  import pubsub from 'pubsub.js'
  // 发布消息(发布的消息必须是List类型)
  pubsub.publish('消息名', ['data'])
  // 订阅消息(若不会用到unsubscribe, 则可以不写this.subId=)
  this.subId = pubsub.subscribe('消息名', (data) => {
    // 接收的数据
    console.log(data)
  })
  // 取消订阅(需要传递一个ID)
  pubsub.unsubscribe(subId)
                </pre>
            </div>
        </div>
    </div>
</template>

<script>
import AreaVue from '@/common/Area.vue';
import ReceiveVue from '@/common/Receive.vue';

export default {
    name: 'NewsPublish',
    components: { AreaVue, ReceiveVue },
    data() {
        return {};
    },
};
</script>

