<template>
    <div>
        <p>我是消息发布者</p>
        <p>
            <button
                type="button"
                @click="publishInfo">
                发布一条消息
            </button>
            &ensp;
            <input
                type="text"
                placeholder="请输入要发布的信息"
                v-model="info" />
        </p>
    </div>
</template>

<script>
import pubsub from 'pubsub.js';

export default {
    name: 'Publish',
    data() {
        return {
            info: null,
        };
    },
    methods: {
        publishInfo() {
            // 发布消息
            pubsub.publish('news', [
                this.info ? this.info : '2023到了, 祝你心想事成, 万事如意!',
            ]);
            this.info = null;
        },
    },
};
</script>

