<template>
    <div>
        <p>我是消息接收者</p>
        <hr />
        <div>
            接收的信息:
            <p
                v-for="(item, i) in receiveInfo"
                :key="i">
                {{ item }}
            </p>
        </div>
    </div>
</template>

<script>
import pubsub from 'pubsub.js';

export default {
    name: 'Receive',
    data() {
        return {
            receiveInfo: Array(),
        };
    },
    mounted() {
        this.subId = pubsub.subscribe('news', (data) => {
            console.log('发布消息了.');
            this.receiveInfo.push(data);
        });
    },
    beforeDestroy() {
        pubsub.unsubscribe(this.subId);
    },
};
</script>

<style scoped></style>
